
import { defineComponent, SetupContext } from 'vue';
export default defineComponent({
  components: {
    
  },
  props: {
    modelValue: {
      type: Number
    }
  },
  emits: [
    'update:modelValue'
  ],
  setup(props: any, context: SetupContext ) {
    const changeTo = (val: number) => {
      context.emit('update:modelValue', val);
    }
    return {
      changeTo
    }
  }
})
